.pricing-grids {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

  .pricing-grid1,
  .pricing-grid2,
  .pricing-grid3 {
    width: 30%;
    float: left;
    text-align: center;
    margin-right: 2%;
    transition: 0.5s all;
    padding: 0;
    border: none;
    border-radius: 0.7em;
  }
  .pricing-grid3 {
    margin-right: 0;
  }
  .pricing-grid1:hover,
  .pricing-grid2:hover,
  .pricing-grid3:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    z-index: 1;
    border: none;
  }
  .pricing-grid1:nth-child(3) {
    margin-right: 0;
  }
  .price-value h2 a,
  .price-value.two h3 a,
  .price-value.three h4 a {
    font-size: 1.8em;
    color: #fff;
  }
  .price-value,
  .price-value.two,
